body{
  .footer{
    &__nav{
      background-color: #e2e2e2;
      .container{
        .row{
          .col-lg-3{
            display: flex;
            justify-content: space-around;
            ul{
              margin: 20px 0;
              li{
                a{
                  display: block;
                  font: .938em/1.375em "Arial";
                  color: #0081ef;
                  text-decoration: none;
                  &:hover{
                    text-decoration: underline;
                  }
                }
              }
            }
          }
          .col-lg-2{
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            ul{
              &:nth-child(1){
                margin: 20px 0 10px;
              }
              &:nth-child(2){
                margin: 10px 0 20px;
              }
              li{
                a{
                  display: block;
                  font: .938em/1.375em "Arial";
                  color: #0081ef;
                  text-decoration: none;
                  &:hover{
                    text-decoration: underline;
                  }
                }
              }
            }
          }
          .col-lg-4{
            ul{
              margin: 20px 0;
              li{
                a{
                  display: block;
                  font: .938em/1.375em "Arial";
                  color: #0081ef;
                  text-decoration: none;
                  span{
                    &:nth-child(1){
                      text-transform: uppercase;
                    }
                  }
                  &:hover{
                    text-decoration: underline;
                  }
                }
              }
            }
          }
        }
      }
    }
    &__info{
      background-color: #d8d8d8;
      .container{
        .row{
          .col-lg-4{
            .copyright{
              margin: 20px 0;
              p{
                font: .875em/1.25em "Arial Narrow";
                color: #626262;
              }
            }
            .payments{
              margin: 10px 0;
              p{
                margin: 0 5px;
                font: .875em/1.25em "Arial";
                color: #676767;
              }
              ul{
                display: flex;

                li{
                  margin: 5px;
                }
              }
            }
          }
        }
      }
    }
  }
}